// src/pages/Skills.js
import React from "react";
import { motion } from "framer-motion";
import { Container, Typography, Grid, Box } from "@mui/material";


// src/data/skillsData.js
const skillsData = [
  {
    title: "Dveloppement Web",
    skills: [
      { name: "HTML & CSS", description: "Cration de pages web avec HTML5 et CSS3, responsive design." },
      { name: "JavaScript & TypeScript", description: "Dveloppement d'applications dynamiques et interactives." },
      { name: "React & Redux", description: "Dveloppement de composants React et gestion de l'tat global avec Redux." },
    ],
  },
  {
    title: "Dveloppement Logiciel",
    skills: [
      { name: "Python", description: "Dveloppement de scripts et d'applications backend." },
      { name: "Java", description: "Cration d'applications robustes et maintenables." },
    ],
  },
  {
    title: "Cyberscurit",
    skills: [
      { name: "Scurit Rseau", description: "Protection et scurisation des rseaux d'entreprise." },
      { name: "Audit de Scurit", description: "valuation de la scurit des systmes et des applications." },
    ],
  },
  {
    title: "Cloud et Virtualisation",
    skills: [
      { name: "AWS & Azure", description: "Dploiement et gestion d'infrastructures cloud." },
      { name: "Docker & Kubernetes", description: "Orchestration de conteneurs pour une meilleure gestion des applications." },
    ],
  },
];



const Skills = () => {
  return (
    <Container>
      <Typography class="Heading ">Mes Comptences</Typography>
      <Typography class="Subheading" > 
        Voici un aperu des technologies et comptences que je matrise dans les
        domaines du dveloppement web, du dveloppement logiciel, de la
        cyberscurit, et du cloud et de la virtualisation.
      </Typography>
      {skillsData.map((category, index) => (
        <Box key={index}>
          <Typography class="Subheading">{category.title}</Typography>
          <Grid>
            {category.skills.map((skill, idx) => (
              <Grid>
                <Box>
                  <Typography class="Body1">{skill.name}</Typography>
                  <Typography class="Body2">{skill.description}</Typography>
                </Box>
              </Grid>
            ))}
          </Grid>
        </Box>
      ))}
    </Container>
  );
};

export default Skills;
